.Rightbar__container {
  -ms-flex: 1 1;
      flex: 1 1; }

.Rightbar__card {
  margin: 32px;
  min-height: 80%; }

.Rightbar__header {
  width: 100%;
  padding: 32px 32px 0 !important; }

.Rightbar__title {
  margin: 0;
  font-size: 22px;
  font-weight: 600;
  color: #222;
  line-height: 1.2; }

.Rightbar__desc {
  margin: 0;
  font-size: 13px;
  color: #999; }

.Rightbar__tag {
  margin: 20px !important; }
  .Rightbar__tag span {
    font-weight: 700 !important;
    color: #fff !important;
    margin: 0 auto; }
  .Rightbar__tag.compliant {
    background: #4BB543 !important; }
  .Rightbar__tag.noncompliant {
    background: #cc0000 !important; }



